import React, {Component} from 'react';
import BoardService from "../service/BoardService";

class ReadBoardComponent extends Component {
    constructor(props) {
        super(props);

        this.state = {
            id: this.props.match.params.id,
            board: {}
        }
    }

    componentDidMount() {
        BoardService.getOneBoard(this.state.id).then(res => {
            console.log(res.data);
            this.setState({board: res.data})
        });
    }

    returnBoardType(typeNo) {
        let type = null;

        switch (typeNo) {
            case 1:
                type = "자유게시판";
                break;
            case 2:
                type = "질문과 답변";
                break;
            default:
                type = "타입 미지정";
                break;
        }

        return (
            <div className="row">
                <label>Board Type : </label> {type}
            </div>
        );
    }

    returnDate(cTime, uTime) {
        return (
            <div className="row">
                <label>작성일 : [{cTime}] / 수정일 : [{uTime}]</label>
            </div>
        );
    }

    goToList() {
        this.props.history.push('/board');
    }

    render() {
        return (
            <div>
                <div className="card col-md-6 offset-md-3">
                    <h3 className="text-center"> Read Detail</h3>
                    <div className="card-body">
                        {this.returnBoardType(this.state.board.type)}
                        <div className="row">
                            <label> Title </label> : {this.state.board.title}
                        </div>

                        <div className="row">
                            <label> Contents </label> : <br/>
                            <textarea value={this.state.board.contents} readOnly/>
                            <div className="form-group">
                                <label htmlFor="exampleFormControlTextarea1">Example textarea</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"/>
                            </div>
                        </div>


                        {this.returnDate(this.state.board.createdTime, this.state.board.updatedTime)}

                        <button className="btn btn-primary" onClick={this.goToList.bind(this)}
                                style={{marginLeft: "10px"}}>글 목록으로 이동
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

export default ReadBoardComponent;